/* footer css */

.bg-footer {
  background-color: #000;
  padding: 20px 25px 0px;
  /* border: 1px solid red; */
  font-family: "Maven Pro", sans-serif;
}

.container {
  margin-bottom: -20px;
  width: 100%;
}
.fCol {
  /* border: 1px solid red; */
  width: auto;
}

@media screen and (max-width: 991px) {
  .fCol {
    margin-bottom: 20px;
  }
  .container {
    margin-bottom: -50px;
  }
}

.footer-heading {
  letter-spacing: 3px;
  font-weight: 700;
}

.footer-link a {
  color: white;
  line-height: 30px;
  font-size: 14px;
  transition: all 0.5s;
  text-decoration: none;
  font-weight: 500;
}

.footer-link a:hover {
  color: #fe8235;
  line-height: 40px;
  border-bottom: 0.5px solid #fe8235;
}

.contact-info {
  color: white;
  font-size: 14px;
  font-weight: 500;
}

.contact-info-icon {
  color: white;
  margin-right: 6px;
  cursor: pointer;
}

.contact-info-icon:hover {
  color: #fe8235;
}

.footer-alt {
  color: white;
}

.footer-alt :hover {
  color: #fe8235;
}

.footer-heading {
  position: relative;
  padding-bottom: 10px;
  border-bottom: 2.5px solid #fe8235;
  width: 121px;
  margin-bottom: -10px;
}

.copyright {
  position: relative;
  top: 5px;
  font-weight: 500;
}
